// Baptist church brand colors
$primary-green: #399344;
$primary-blue: #429aca;
$accent-yellow: #eac338;
$white: #ffffff;
$light-gray: #f8f9fa;
$dark-text: #333333;

// Page Header
.page-header {
  background: linear-gradient(135deg, $primary-green 0%, $primary-blue 100%);
  color: $white;
  padding: 4rem 0 3rem;
  text-align: center;
  position: relative;
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><circle cx="50" cy="50" r="1" fill="white" opacity="0.1"/></svg>') repeat;
    animation: float 15s ease-in-out infinite;
  }
  
  h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
    color: $white;
    position: relative;
    z-index: 2;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  }
  
  p {
    font-size: 1.2rem;
    opacity: 0.9;
    position: relative;
    z-index: 2;
  }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-15px); }
}

// News Section
.news-section {
  background: linear-gradient(135deg, $light-gray 0%, rgba($primary-blue, 0.05) 100%);
  
  .news-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
  }
  
  .news-card {
    background: $white;
    border-radius: 20px;
    padding: 2rem;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: all 0.4s ease;
    border: 1px solid rgba($primary-blue, 0.1);
    position: relative;
    overflow: hidden;
    
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 4px;
      background: linear-gradient(90deg, $primary-green 0%, $primary-blue 50%, $accent-yellow 100%);
    }
    
    &:hover {
      transform: translateY(-10px) scale(1.02);
      box-shadow: 0 20px 50px rgba(0, 0, 0, 0.15);
      border-color: rgba($primary-blue, 0.3);
    }
    
    &.featured {
      background: linear-gradient(135deg, rgba($primary-green, 0.08) 0%, rgba($primary-blue, 0.08) 100%);
      border: 2px solid rgba($primary-green, 0.2);
      
      &::before {
        height: 6px;
        background: linear-gradient(90deg, $primary-green 0%, $accent-yellow 100%);
      }
      
      h2, h3 {
        color: $primary-green;
        font-size: 1.4rem;
        margin-bottom: 1rem;
      }
    }
    
    .news-date {
      display: inline-block;
      background: linear-gradient(135deg, $primary-blue 0%, $primary-green 100%);
      color: $white;
      padding: 0.5rem 1rem;
      border-radius: 20px;
      font-size: 0.85rem;
      font-weight: 600;
      margin-bottom: 1rem;
      text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
    }
    
    h2, h3 {
      color: $primary-green;
      margin-bottom: 1rem;
      font-weight: 600;
      line-height: 1.3;
    }
    
    h2 {
      font-size: 1.3rem;
    }
    
    h3 {
      font-size: 1.2rem;
    }
    
    p {
      color: #555;
      line-height: 1.7;
      margin-bottom: 1.5rem;
      font-size: 1rem;
    }
    
    .news-footer {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 1.5rem;
      padding-top: 1rem;
      border-top: 1px solid rgba($primary-blue, 0.1);
      
      .news-category {
        background: rgba($accent-yellow, 0.2);
        color: $dark-text;
        padding: 0.3rem 0.8rem;
        border-radius: 15px;
        font-size: 0.8rem;
        font-weight: 500;
      }
      
      .read-more {
        color: $primary-blue;
        font-weight: 600;
        cursor: pointer;
        transition: all 0.3s ease;
        font-size: 0.9rem;
        
        &:hover {
          color: $primary-green;
          transform: translateX(5px);
        }
      }
    }
  }
}

// Newsletter Section
.newsletter-section {
  background: linear-gradient(135deg, $primary-green 0%, $primary-blue 100%);
  color: $white;
  position: relative;
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 200 200"><polygon points="100,0 150,50 100,100 50,50" fill="white" opacity="0.05"/></svg>') repeat;
    animation: float 20s ease-in-out infinite reverse;
  }
  
  .newsletter-content {
    text-align: center;
    max-width: 600px;
    margin: 0 auto;
    position: relative;
    z-index: 2;
    
    h2 {
      font-size: 2rem;
      margin-bottom: 1rem;
      color: $white;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    }
    
    p {
      font-size: 1.1rem;
      margin-bottom: 2rem;
      opacity: 0.9;
    }
    
    .newsletter-form {
      display: flex;
      gap: 1rem;
      margin-bottom: 1rem;
      max-width: 400px;
      margin-left: auto;
      margin-right: auto;
      
      @media (max-width: 768px) {
        flex-direction: column;
      }
      
      .email-input {
        flex: 1;
        padding: 1rem;
        border: none;
        border-radius: 25px;
        font-size: 1rem;
        outline: none;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
        
        &::placeholder {
          color: #999;
        }
        
        &:focus {
          box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
          transform: translateY(-2px);
        }
      }
      
      .btn {
        padding: 1rem 2rem;
        white-space: nowrap;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
        
        &:hover {
          box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
        }
      }
    }
    
    .newsletter-info {
      font-size: 0.9rem;
      opacity: 0.8;
      font-style: italic;
    }
  }
}

// Responsive Design
@media (max-width: 768px) {
  .page-header {
    padding: 3rem 0 2rem;
    
    h1 {
      font-size: 2rem;
    }
  }
  
  .news-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .news-card {
    padding: 1.5rem;
    
    &:hover {
      transform: translateY(-5px) scale(1.01);
    }
  }
  
  .newsletter-content {
    h2 {
      font-size: 1.6rem;
    }
  }
}

// Animation for news cards on load
.news-card {
  animation: slideInUp 0.6s ease-out;
  animation-fill-mode: both;
  
  @for $i from 1 through 6 {
    &:nth-child(#{$i}) {
      animation-delay: #{$i * 0.1}s;
    }
  }
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
